<template>
  <div>
    <h1>Lol</h1>
      Time
      <select id="selectTime">
      <option v-for="i in hours">
        {{i}}
      </option>
      </select>
  </div>
</template>

<script>
export default {
  name: "Lol",
  data: () => ({
        hours: [],
      }
  ),
  created() {
    for (let i = 0; i < 24; i++) {
      this.hours[i] =  String(i).padStart(2, '0');
    }
  }
}
</script>